import React from 'react'
import { Mutation, Query } from 'react-apollo'
import gql from 'graphql-tag'
import styled from 'styled-components'
import PropTypes from 'prop-types'
import { CURRENT_USER_QUERY } from './User'

const REMOVE_FROM_CART_MUTATION = gql`
  mutation removeFromCart($id: ID!) {
    removeFromCart(id: $id) {
      id
    }
  }
`

const BigButton = styled.button`
  font-size: 3rem;
  background: none;
  border: 0;
  &:hover {
    color: ${props => props.theme.red};
    cursor: pointer;
  }
`;

class RemoveFromCart extends React.Component {
  static propTypes = {
    id: PropTypes.string.isRequired
  }
  update = (cache, payload) => {
    console.log('Running remove from cart')
    const data = cache.readQuery({
      query: CURRENT_USER_QUERY
    })
    console.log(data)
    const cartItemId = payload.data.removeFromCart.id
    data.me.cart = data.me.cart.filter(cartItem => cartItem.id !==cartItemId)
    cache.writeQuery({ query: CURRENT_USER_QUERY , data})
  }
  render() {
    return <Mutation variables={{id: this.props.id}} update={this.update}  mutation={REMOVE_FROM_CART_MUTATION}>{(removeFromCart, { loading, error }) => (
      <BigButton disabled={loading} onClick={() => {removeFromCart().catch(err => alert(err.message))}} title="Delete Item">&times;</BigButton>
    )}</Mutation>   
  }
}

export default RemoveFromCart